import { Directive, HostListener, HostBinding, AfterViewInit, ElementRef, Input } from '@angular/core';
import { ViewportScroller } from '@angular/common';

@Directive({
  selector: '[pbdsHeaderShadow]'
})
export class PbdsHeaderShadowDirective implements AfterViewInit {
  @HostBinding('class.pbds-header-shadow')
  shadow: boolean;

  @Input('pbdsHeaderShadow') item: null;

  @HostListener('window:scroll', []) onWindowScroll() {
    const offset = this._scroll.getScrollPosition();
    this.shadow = offset[1] > 20;
  }

  constructor(private _scroll: ViewportScroller, private _element: ElementRef) {}

  ngAfterViewInit() {
    if (this.item) {
      const div = document.body.querySelector(`${this.item}`);
      div.addEventListener('scroll', event => {
        this.shadow = event.srcElement.scrollTop > 20;
      });
    }
  }
}
