import {
  Component,
  OnInit,
  Input,
  ElementRef,
  HostBinding,
  OnChanges,
  SimpleChanges,
  Output,
  EventEmitter
} from '@angular/core';

import {
  select as d3_select,
  scaleOrdinal as d3_scaleOrdinal,
  pie as d3_pie,
  arc as d3_arc,
  interpolate as d3_interpolate,
  mouse as d3_mouse,
  format as d3_format
} from 'd3';

import { PbdsDatavizService } from './dataviz.service';
import { IPbdsDatavizPie } from './dataviz.interfaces';

@Component({
  selector: 'pbds-dataviz-pie',
  template: ``,
  styleUrls: []
})
export class PbdsDatavizPieComponent implements OnInit, OnChanges {
  @HostBinding('class.pbds-chart')
  chartClass = true;

  @HostBinding('class.pbds-chart-pie')
  pieClass = true;

  @Input()
  data: Array<IPbdsDatavizPie>;

  @Input()
  width = 300;

  @Input()
  type: 'pie' | 'donut' = 'pie';

  @Input()
  mono = false;

  // TODO: do we need a tooltipFormatType?

  @Input()
  tooltipFormatString = '';

  // TODO: add input for legendFormatType?

  @Input()
  legendFormatString = '';

  @Output()
  hovered: EventEmitter<object> = new EventEmitter<object>();

  @Output()
  clicked: EventEmitter<object> = new EventEmitter<object>();

  private height;
  private chart;
  private margin;
  private colors;
  private colorRange;
  private arc;
  private arcZoom;
  private svg;
  private pie;
  private legend;
  private legendFormat;
  private innerRadius;
  private anglePad;
  private outerRadius;
  private paths;
  private tooltip;
  private tooltipFormat;

  constructor(private _dataviz: PbdsDatavizService, private _element: ElementRef) {}

  ngOnInit() {
    this.margin = { top: 10, right: 10, bottom: 10, left: 10 };
    this.width = this.width - this.margin.left - this.margin.right;
    this.height = this.width - this.margin.top - this.margin.bottom;
    this.colors = this._dataviz.getColors(this.mono);
    this.innerRadius = Math.min(this.width, this.height) / 2.5;
    this.outerRadius = Math.min(this.width, this.height) / 2;
    this.arcZoom = 10;
    this.anglePad = 0.02;
    this.legendFormat = d3_format(this.legendFormatString);
    this.tooltipFormat = d3_format(this.tooltipFormatString);

    this.colorRange = d3_scaleOrdinal()
      .range(this.colors)
      .domain(this.data.map(c => c.label));

    if (this.type === 'pie') {
      this.innerRadius = 0;
      this.anglePad = 0;
    }

    this.chart = d3_select(this._element.nativeElement).attr('aria-hidden', 'true');

    this.svg = this.chart
      .append('svg')
      .attr('width', this.width)
      .attr('height', this.height)
      .attr('class', 'img-fluid')
      .attr('preserveAspectRatio', 'xMinYMin meet')
      .attr(
        'viewBox',
        `-${this.width / 2 + this.margin.left} -${this.height / 2 + this.margin.top} ${this.width +
          this.margin.left +
          this.margin.right} ${this.height + this.margin.top + this.margin.bottom}`
      );

    this.pie = d3_pie()
      .padAngle(this.anglePad)
      .value((d: any) => d.value);

    this.arc = d3_arc()
      .padRadius(this.outerRadius)
      .innerRadius(this.innerRadius);

    this.paths = this.svg
      .selectAll('path')
      .data(this.pie(this.data))
      .enter()
      .append('path')
      .each((d: any) => (d.outerRadius = this.outerRadius))
      .attr('d', this.arc)
      .attr('fill', (d: any) => this.colorRange(d.data.label))
      .attr('class', 'slice');

    if (this.type === 'pie') {
      this.paths
        .style('stroke', '#fff')
        .style('stroke-width', 2)
        .style('stroke-alignment', 'inner');
    }

    this.legend = this.chart
      .append('ul')
      .attr('class', 'legend legend-right')
      .selectAll('li')
      .data(this.data)
      .enter()
      .append('li')
      .attr('tabindex', 0)
      .attr('class', 'legend-item');

    this.legend
      .append('span')
      .attr('class', 'legend-key')
      .style('background-color', (d: any) => this.colorRange(d.label));

    let legendDescription = this.legend.append('span').attr('class', 'legend-description');

    legendDescription
      .append('span')
      .attr('class', 'legend-label')
      .html((d: any) => d.label);

    legendDescription
      .append('span')
      .attr('class', 'legend-value')
      .html((d: any) => this.legendFormat(d.value));

    this.legend
      .on('mouseover focus', (data, index, nodes) => {
        this.legendMouseOverFocus(data, index, nodes);
        this.pathMouseOver(data, index, nodes);
      })
      .on('mouseout blur', (data, index, nodes) => {
        this.legendMouseOutBlur(data, index, nodes);
        this.pathMouseOut(data, index, nodes);
      })
      .on('click', (data, index, nodes) => {
        this.clicked.emit(data);
      });

    this.paths
      .on('mouseover', (data, index, nodes) => {
        this.pathMouseOver(data, index, nodes);
        this.tooltipShow(this.chart.node(), data);
      })
      .on('mousemove', (data, index, nodes) => {
        this.tooltipMove(this.chart.node());
      })
      .on('mouseout', (data, index, nodes) => {
        this.pathMouseOut(data, index, nodes);
        this.tooltipHide();
      })
      .on('click', (data, index, nodes) => {
        this.pathClick(data, index, nodes);
      });

    this.tooltip = this.chart
      .append('div')
      .style('opacity', 0)
      .attr('class', 'pbds-tooltip')
      .attr('aria-hidden', 'true');
  }

  ngOnChanges(changes: SimpleChanges) {
    if ((changes.data && !changes.data.firstChange) || (changes.mono && !changes.mono.firstChange)) {
      // TODO: refactor to use update() function instead
      this.chart.selectAll('svg').remove();
      this.chart.selectAll('ul').remove();
      this.chart.selectAll('div').remove();

      this.ngOnInit();
    }
  }

  private legendMouseOverFocus = (data, index, nodes) => {
    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);
  };

  private legendMouseOutBlur = (data, index, nodes) => {
    this.chart.selectAll('.legend-item').classed('inactive', false);
  };

  private pathMouseOver = (data, index, nodes) => {
    let slices = this.chart.selectAll('.slice');
    let slice = slices.filter((d, i) => i === index);

    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    slices.filter((d, i) => i !== index).classed('inactive', true);

    slice
      .transition()
      .duration(300)
      .delay(0)
      .attrTween('d', (d: any) => {
        let i = d3_interpolate(d.outerRadius, this.outerRadius + this.arcZoom);
        return t => {
          d.outerRadius = i(t);
          return this.arc(d);
        };
      });

    this.hovered.emit(data.data ? data.data : data); // legend hover data is different than slice hover data
  };

  private pathMouseOut = (data, index, value) => {
    let slices = this.chart.selectAll('.slice');
    let slice = slices.filter((d, i) => i === index);

    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', false);

    slices.classed('inactive', false);

    slice
      .transition()
      .duration(300)
      .delay(0)
      .attrTween('d', (d: any) => {
        let i = d3_interpolate(d.outerRadius, this.outerRadius);
        return t => {
          d.outerRadius = i(t);
          return this.arc(d);
        };
      });
  };

  private pathClick = (data, index, nodes) => {
    this.clicked.emit(data.data);
  };

  private tooltipShow = (node, data) => {
    this.tooltipSetPosition(node);

    let percentage = (data.endAngle - data.startAngle) / (2 * Math.PI);

    this.tooltip.html(
      `
        <div class="tooltip-label">${data.data.label}</div>
        <div class="tooltip-value">${this.tooltipFormat(percentage)}</div>
      `
    );

    this.tooltip.style('opacity', 1);
  };

  private tooltipMove = node => {
    this.tooltipSetPosition(node);
  };

  private tooltipHide = () => {
    this.tooltip.style('opacity', 0);
  };

  private tooltipSetPosition = node => {
    let coordinates = d3_mouse(node);

    this.tooltip.style('left', `${coordinates[0] + 16}px`);
    this.tooltip.style('top', `${coordinates[1] + 16}px`);
  };
}
