import { Component, OnInit, Input, ElementRef, HostBinding } from '@angular/core';

import {
  select as d3_select,
  line as d3_line,
  scaleLinear as d3_scaleLinear,
  min as d3_min,
  max as d3_max,
  area as d3_area
} from 'd3';

import { IPbdsDatavizSparkline } from './dataviz.interfaces';

@Component({
  selector: 'pbds-dataviz-sparkline',
  template: ``,
  styles: []
})
export class PbdsDatavizSparklineComponent implements OnInit {
  @HostBinding('class.pbds-chart')
  chartClass = true;

  @HostBinding('class.pbds-chart-sparkline')
  sparklineClass = true;

  @Input()
  data: IPbdsDatavizSparkline;

  @Input()
  width = 160;

  @Input()
  height = 80;

  @Input()
  color = '#cf0989';

  @Input()
  colorNegative = '#3e53a4';

  @Input()
  type: 'line' | 'area' | 'bar' = 'line';

  @Input()
  strokeWidth = 2;

  @Input()
  minBuffer = 0;

  @Input()
  maxBuffer = 0;

  private chart;
  private svg;
  private margin;

  constructor(private _element: ElementRef) {}

  ngOnInit() {
    this.margin = { top: 1, right: 0, bottom: 1, left: 0 };

    if (this.type === 'bar') {
      this.margin = { top: 0, right: 0, bottom: 0, left: 0 };
    }

    let x = d3_scaleLinear().range([0, this.width - this.margin.left - this.margin.right]);
    let y = d3_scaleLinear().range([this.height - this.margin.top - this.margin.bottom, 0]);

    y.domain([+d3_min(this.data) - this.minBuffer, +d3_max(this.data) + this.maxBuffer]);
    x.domain([0, this.data.length]);

    let line = d3_line()
      .x((d, i) => x(i))
      .y((d: any) => y(d));

    let area = d3_area()
      .x((d, i) => x(i))
      .y0(this.height)
      .y1((d: any) => y(d));

    this.chart = d3_select(this._element.nativeElement).attr('aria-hidden', 'true');

    this.svg = this.chart
      .append('svg')
      .attr('width', this.width)
      .attr('height', this.height)
      .attr('class', 'img-fluid')
      .attr('preserveAspectRatio', 'xMinYMin meet')
      .attr('viewBox', `-${this.margin.left} -${this.margin.top} ${this.width} ${this.height}`);

    if (this.type === 'line' || this.type === 'area') {
      this.svg
        .append('path')
        .datum(this.data)
        .attr('class', 'sparkline')
        .attr('fill', 'none')
        .attr('stroke-width', this.strokeWidth)
        .attr('stroke', this.color)
        .attr('d', line);
    }

    if (this.type === 'area') {
      this.svg
        .append('path')
        .datum(this.data)
        .attr('class', 'sparkarea')
        .attr('fill', this.color)
        .attr('fill-opacity', 0.3)
        .attr('d', area);
    }

    if (this.type === 'bar') {
      const barWidth = (this.width - this.data.length) / this.data.length;

      // handles negative values, see example https://www.essycode.com/posts/create-sparkline-charts-d3/
      this.svg
        .selectAll('.bar')
        .data(this.data)
        .enter()
        .append('rect')
        .attr('class', 'sparkbar')
        .attr('x', (d, i) => x(i))
        .attr('y', d => (d > 0 ? y(d) : y(0)))
        .attr('width', barWidth)
        .attr('height', d => Math.abs(y(d) - y(0)))
        .attr('fill', d => (d > 0 ? this.color : this.colorNegative));
    }
  }
}
