import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  ElementRef,
  HostBinding,
  OnDestroy,
  SimpleChanges,
  ChangeDetectionStrategy
} from '@angular/core';

import { ViewportScroller } from '@angular/common';

import {
  select as d3_select,
  isoParse as d3_isoParse,
  timeFormat as d3_timeFormat,
  format as d3_format,
  scaleOrdinal as d3_scaleOrdinal,
  scaleBand as d3_scaleBand,
  scaleLinear as d3_scaleLinear,
  min as d3_min,
  max as d3_max,
  axisBottom as d3_axisBottom,
  axisLeft as d3_axisLeft,
  event as d3_event
} from 'd3';

import { PbdsDatavizService } from './dataviz.service';
import { IPbdsDatavizBar } from './dataviz.interfaces';

@Component({
  selector: 'pbds-dataviz-bar',
  template: ``,
  styles: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PbdsDatavizBarComponent implements OnInit, OnDestroy, OnChanges {
  @HostBinding('class.pbds-chart')
  chartClass = true;

  @HostBinding('class.pbds-chart-bar')
  barClass = true;

  @Input()
  data: IPbdsDatavizBar[];

  @Input()
  width = 306;

  @Input()
  height = 400;

  @Input()
  type: 'low' | 'medium' | 'high' | 'debug' = 'medium'; // debug to show all chart options

  @Input()
  singleSeries = false;

  @Input()
  xAxisFormatType: 'number' | 'time' = null;

  @Input()
  xAxisFormatString = '';

  @Input()
  yAxisFormatType: 'number' | 'time' = null;

  @Input()
  yAxisFormatString = '';

  @Input()
  yAxisTicks = 5;

  @Input()
  yAxisBuffer = 0.01;

  @Input()
  hideLegend = false;

  @Input()
  legendWidth = 105 + 28; // hardcoded legend width + left margin, do not document until feedback

  @Input()
  legendPosition: 'right' | 'bottom' = 'right';

  @Input()
  legendLabelFormatType: 'number' | 'time' = null;

  @Input()
  legendLabelFormatString = '';

  @Input()
  tooltipLabelFormatType: 'number' | 'time' = null;

  @Input()
  tooltipLabelFormatString = '';

  @Input()
  tooltipValueFormatType: 'number' | 'time' = null;

  @Input()
  tooltipValueFormatString = '';

  @Input()
  marginTop = 0; // hardcoded on purpose, do not document until feedback

  @Input()
  marginRight = 0; // hardcoded on purpose, do not document until feedback

  @Input()
  marginBottom = 30; // hardcoded on purpose, do not document until feedback

  @Input()
  marginLeft = 55; // hardcoded on purpose, do not document until feedback

  @Input()
  threshold = null;

  @Input()
  average = null;

  @Output()
  hovered: EventEmitter<object> = new EventEmitter<object>();

  @Output()
  clicked: EventEmitter<object> = new EventEmitter<object>();

  private chart;
  private svg;
  private margin;
  private colorRange;
  private hideGrayBars: boolean;
  private yThreshold;
  private yAverage;
  private xAxisScale;
  private xAxisCall;
  private xAxis;
  private xAxisFormat;
  private yAxisScale;
  private yAxisCall;
  private yAxis;
  private yAxisFormat;
  private xGrid;
  private xGridCall;
  private yGrid;
  private yGridCall;
  private xAxisTickSize: number;
  private xAxisTickSizeOuter: number;
  private yAxisTickSize: number;
  private yAxisTickSizeOuter: number;
  private hideXAxis: boolean;
  private hideYAxis: boolean;
  private hideXAxisDomain: boolean;
  private hideYAxisDomain: boolean;
  private hideXAxisZero: boolean;
  private hideYAxisZero: boolean;
  private hideXGrid: boolean;
  private hideYGrid: boolean;
  private hideXAxisTicks: boolean;
  private hideYAxisTicks: boolean;
  private legendLabelFormat;
  private tooltip;
  private hideTooltip: boolean;
  private hideTooltipLabel: boolean;
  private tooltipValueFormat;
  private tooltipLabelFormat;

  constructor(private _dataviz: PbdsDatavizService, private _element: ElementRef, private _scroll: ViewportScroller) {}

  ngOnInit() {
    this.margin = {
      top: +this.marginTop,
      right: +this.marginRight,
      bottom: +this.marginBottom,
      left: +this.marginLeft
    };

    switch (this.xAxisFormatType) {
      case 'number':
        this.xAxisFormat = d3_format(this.xAxisFormatString);
        break;
      case 'time':
        this.xAxisFormat = d3_timeFormat(this.xAxisFormatString);
        break;
    }

    switch (this.yAxisFormatType) {
      case 'number':
        this.yAxisFormat = d3_format(this.yAxisFormatString);
        break;
      case 'time':
        this.yAxisFormat = d3_timeFormat(this.yAxisFormatString);
        break;
    }

    switch (this.legendLabelFormatType) {
      case 'number':
        this.legendLabelFormat = d3_format(this.legendLabelFormatString);
        break;
      case 'time':
        this.legendLabelFormat = d3_timeFormat(this.legendLabelFormatString);
        break;
      default:
        this.legendLabelFormat = null;
        break;
    }

    switch (this.tooltipLabelFormatType) {
      case 'number':
        this.tooltipLabelFormat = d3_format(this.tooltipLabelFormatString);
        break;
      case 'time':
        this.tooltipLabelFormat = d3_timeFormat(this.tooltipLabelFormatString);
        break;
      default:
        this.tooltipLabelFormat = null;
        break;
    }

    switch (this.tooltipValueFormatType) {
      case 'number':
        this.tooltipValueFormat = d3_format(this.tooltipValueFormatString);
        break;
      case 'time':
        this.tooltipValueFormat = d3_timeFormat(this.tooltipValueFormatString);
        break;
      default:
        this.tooltipValueFormat = null;
    }

    // defaults for all chart types
    this.hideGrayBars = false;
    this.hideXAxis = false;
    this.hideYAxis = false;
    this.hideXAxisZero = false;
    this.hideYAxisZero = false;
    this.hideXGrid = false;
    this.hideYGrid = false;
    this.hideXAxisDomain = false;
    this.hideYAxisDomain = false;
    this.hideTooltip = false;
    this.hideXAxisTicks = false;
    this.hideYAxisTicks = false;
    this.xAxisTickSize = 8;
    this.xAxisTickSizeOuter = 0;
    this.yAxisTickSize = 8;
    this.yAxisTickSizeOuter = 0;
    this.hideTooltipLabel = false;

    if (this.type !== 'debug') {
      // set type defaults
      switch (this.type) {
        case 'low':
          this.hideGrayBars = true;
          this.hideXAxis = !this.hideLegend;
          this.hideXAxisTicks = true;
          this.hideXGrid = true;
          this.hideYAxisDomain = false;
          this.hideYAxisTicks = true;
          this.legendPosition = 'bottom';
          this.hideTooltipLabel = true;
          break;

        case 'medium':
          this.hideXAxisDomain = true;
          this.hideXAxis = !this.hideLegend;
          this.hideXGrid = true;
          this.hideXAxisTicks = true;
          this.hideYAxisDomain = true;
          this.hideYAxisTicks = true;
          this.hideYGrid = true;
          this.hideTooltipLabel = true;
          break;

        case 'high':
          this.hideXAxis = true;
          this.hideXAxisDomain = true;
          this.hideXGrid = true;
          this.hideYAxisDomain = true;
          this.hideYAxisTicks = true;
          this.hideYGrid = true;
          this.hideLegend = true;
          this.hideTooltipLabel = false;
          break;
      }

      // single series overrides
      if (this.singleSeries) {
        this.hideLegend = true;
        this.hideXAxis = true;
        this.hideXGrid = true;
        this.hideTooltipLabel = false;
      }
    }

    // adjust margin if xAxis hidden
    if (this.hideXAxis) this.margin.bottom = 10; // need small margin for yAxis with 0 tick label

    if (!this.hideLegend && this.legendPosition === 'right') {
      this.width = +this.width - +this.legendWidth;
    }

    // create the chart
    this.chart = d3_select(this._element.nativeElement).attr('aria-hidden', 'true');

    // create chart svg
    this.svg = this.chart
      .append('svg')
      .attr('width', +this.width)
      .attr('height', +this.height + this.margin.top + this.margin.bottom)
      .attr('class', 'img-fluid')
      .attr('preserveAspectRatio', 'xMinYMin meet')
      .attr(
        'viewBox',
        `-${this.margin.left} -${this.margin.top} ${+this.width} ${+this.height + this.margin.top + this.margin.bottom}`
      );

    // build color ranges
    this.colorRange = d3_scaleOrdinal().range(this._dataviz.createGradientDefs(this.svg, this.singleSeries));

    // X AXIS
    this.xAxisScale = d3_scaleBand()
      .domain(this.data.map(d => d.label))
      .rangeRound([0, this.width - this.margin.left])
      .align(0);

    // add padding to the scale for gray bars
    !this.hideGrayBars
      ? this.xAxisScale.paddingInner(0.1).paddingOuter(0)
      : this.xAxisScale.paddingInner(0).paddingOuter(0);

    this.xAxisCall = d3_axisBottom(this.xAxisScale)
      .tickSize(this.xAxisTickSize)
      .tickSizeOuter(this.xAxisTickSizeOuter)
      .tickFormat(this.xAxisFormatter);

    this.xAxis = this.svg
      .append('g')
      .attr('class', 'axis axis-x')
      .attr('transform', `translate(0, ${this.height})`)
      .classed('axis-hidden', this.hideXAxis)
      .classed('axis-zero-hidden', this.hideXAxisZero)
      .classed('axis-domain-hidden', this.hideXAxisDomain)
      .classed('axis-ticks-hidden', this.hideXAxisTicks)
      .call(this.xAxisCall);

    // X GRIDLINES
    if (!this.hideXGrid) {
      this.xGridCall = d3_axisBottom(this.xAxisScale).tickSize(-this.height);

      this.xGrid = this.svg
        .append('g')
        .attr('class', 'grid grid-x')
        .classed('grid-zero-hidden', this.hideXAxisZero)
        .attr('transform', `translate(0, ${this.height})`)
        .call(this.xGridCall);
    }

    // Y AXIS
    this.yAxisScale = d3_scaleLinear()
      .domain([d3_min(this.data, d => d.value), d3_max(this.data, d => d.value + d.value * +this.yAxisBuffer)])
      .nice()
      .rangeRound([this.height, 0]);

    this.yAxisCall = d3_axisLeft(this.yAxisScale)
      .ticks(this.yAxisTicks)
      .tickSize(this.yAxisTickSize)
      .tickSizeOuter(this.yAxisTickSizeOuter)
      .tickFormat(this.yAxisFormatter);

    this.yAxis = this.svg
      .append('g')
      .attr('class', 'axis axis-y')
      .classed('axis-hidden', this.hideYAxis)
      .classed('axis-zero-hidden', this.hideYAxisZero)
      .classed('axis-domain-hidden', this.hideYAxisDomain)
      .classed('axis-ticks-hidden', this.hideYAxisTicks)
      .call(this.yAxisCall);

    // Y GRIDLINES
    if (!this.hideYGrid) {
      this.yGridCall = d3_axisLeft(this.yAxisScale)
        .ticks(this.yAxisTicks)
        .tickSize(-this.width + this.margin.left + this.margin.right);

      this.yGrid = this.svg
        .append('g')
        .attr('class', 'grid grid-y')
        .classed('grid-zero-hidden', this.hideYAxisZero)
        .attr('transform', `translate(0, 0)`)
        .call(this.yGridCall);
    }

    // Y THRESHOLD
    if (this.threshold) {
      this.yThreshold = this.svg
        .append('line')
        .attr('class', 'threshold')
        .attr('x2', +this.width)
        .attr('transform', `translate(0,  ${this.yAxisScale(+this.threshold)})`);
    }

    // Y AVERAGE
    if (this.average) {
      this.yAverage = this.svg
        .append('line')
        .attr('class', 'average')
        .attr('x2', +this.width)
        .attr('transform', `translate(0,  ${this.yAxisScale(+this.average)})`);
    }

    // TOOLTIP
    if (!this.hideTooltip) {
      this.tooltip = d3_select('body')
        .append('div')
        .attr('class', 'pbds-tooltip south')
        .style('opacity', 0)
        .attr('aria-hidden', 'true'); // hide tooltip for accessibility
    }

    // add legend classes
    if (!this.hideLegend) {
      this.chart.classed('pbds-chart-legend-bottom', this.legendPosition === 'bottom' ? true : false);
      this.chart.append('ul').attr('class', `legend legend-${this.legendPosition}`);
    }

    this.updateChart();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.data && !changes.data.firstChange) {
      this.updateChart();
    }
  }

  ngOnDestroy() {
    if (this.tooltip) this.tooltip.remove();
  }

  updateChart = () => {
    // update the xScale
    this.xAxisScale.domain(this.data.map(d => d.label));

    // update the yScale
    this.yAxisScale
      .domain([d3_min(this.data, d => d.value), d3_max(this.data, d => d.value + d.value * +this.yAxisBuffer)])
      .rangeRound([this.height, 0])
      .nice();

    this.xAxis
      .transition()
      .duration(1000)
      .call(this.xAxisCall);

    this.yAxis
      .transition()
      .duration(1000)
      .call(this.yAxisCall);

    // update the grids
    if (!this.hideXGrid) {
      this.xGrid
        .transition()
        .duration(1000)
        .call(this.xGridCall);
    }

    if (!this.hideYGrid) {
      this.yGrid
        .transition()
        .duration(1000)
        .call(this.yGridCall);
    }

    if (!this.hideGrayBars) {
      // rebind data to groups
      let group = this.svg.selectAll('.bar-group').data(this.data);

      // remove bars
      group.exit().remove();

      // update gray bars
      group
        .select('.gray-bar')
        .transition()
        .duration(1000)
        .attr('x', d => this.xAxisScale(d.label))
        .attr('width', this.xAxisScale.bandwidth());

      // update the existing bars
      group
        .select('.bar')
        .transition()
        .duration(1000)
        .attr('x', d => this.xAxisScale(d.label) + this.xAxisScale.bandwidth() / 4)
        .attr('width', this.xAxisScale.bandwidth() / 2)
        .attr('height', d => this.height - this.yAxisScale(d.value))
        .attr('y', d => this.yAxisScale(d.value));

      // add group on enter
      const groupEnter = group
        .enter()
        .append('g')
        .attr('class', 'bar-group');

      // add gray bars on enter
      groupEnter
        .append('rect')
        .attr('class', 'gray-bar')
        .attr('rx', 0)
        .attr('height', 0)
        .attr('x', d => this.xAxisScale(d.label))
        .attr('width', this.xAxisScale.bandwidth())
        .transition()
        // .delay(1000)
        .attr('height', this.height)
        .attr('width', this.xAxisScale.bandwidth());

      // add bars on enter
      groupEnter
        .append('rect')
        .attr('class', 'bar')
        .attr('rx', 2)
        .attr('fill', d => `url(#gradient-${this.colorRange(d.value).substr(1)})`) // removes hash to prevent safari bug;
        .attr('x', d => this.xAxisScale(d.label) + this.xAxisScale.bandwidth() / 4)
        .attr('width', this.xAxisScale.bandwidth() / 2)
        .attr('y', this.height)
        .attr('height', 0)
        .transition()
        .duration(1000)
        // .delay(1000)
        .attr('y', d => this.yAxisScale(d.value))
        .attr('height', d => this.height - this.yAxisScale(d.value))
        .attr('data-color', d => this.colorRange(d.value));

      groupEnter
        .select('.bar')
        .on('mouseover focus', (data, index, nodes) => this.barMouseOverFocus(d3_event, data, index, nodes))
        .on('mouseout blur', (data, index, nodes) => this.barMouseOutBlur())
        .on('click', (data, index, nodes) => this.barMouseClick(d3_event, data, index, nodes));
    } else {
      // rebind data to groups
      let group = this.svg.selectAll('.bar-group').data(this.data);

      // remove bars
      group.exit().remove();

      // update the existing bars
      group
        .select('.bar')
        .attr('x', d => this.xAxisScale(d.label) + this.xAxisScale.bandwidth() / 5.5)
        .attr('width', this.xAxisScale.bandwidth() / 1.5)
        .transition()
        .duration(1000)
        .attr('y', d => this.yAxisScale(d.value))
        .attr('height', d => this.height - this.yAxisScale(d.value));

      // add group on enter
      const groupEnter = group
        .enter()
        .append('g')
        .attr('class', 'bar-group');

      // add bars on enter
      groupEnter
        .append('rect')
        .attr('class', 'bar')
        .attr('rx', 2)
        .attr('fill', d => `url(#gradient-${this.colorRange(d.value).substr(1)})`) // removes hash to prevent safari bug;
        .attr('x', d => this.xAxisScale(d.label) + this.xAxisScale.bandwidth() / 5.5)
        .attr('width', this.xAxisScale.bandwidth() / 1.5)
        .attr('y', this.height)
        .attr('height', 0)
        .transition()
        .duration(1000)
        .attr('y', d => this.yAxisScale(d.value))
        .attr('height', d => this.height - this.yAxisScale(d.value))
        .attr('data-color', d => this.colorRange(d.value));

      groupEnter
        .select('.bar')
        .on('mouseover focus', (data, index, nodes) => this.barMouseOverFocus(d3_event, data, index, nodes))
        .on('mouseout blur', () => this.barMouseOutBlur())
        .on('click', (data, index, nodes) => this.barMouseClick(d3_event, data, index, nodes));
    }

    if (!this.hideLegend) {
      let legendItem = this.chart
        .select('.legend')
        .selectAll('.legend-item')
        .data(this.data);

      legendItem.exit().remove();

      // update existing items
      legendItem.select('.legend-label').html(d => {
        // return this.legendLabelFormat === null ? d.label : this.legendLabelFormat(d.label);
        switch (this.legendLabelFormatType) {
          case 'number':
            return this.legendLabelFormat(d.label);

          case 'time':
            const parsedTime = d3_isoParse(d.label);
            return this.legendLabelFormat(parsedTime);

          default:
            return d.label;
        }
      });

      // legend items on enter
      let enterLegendItem = legendItem
        .enter()
        .append('li')
        .attr('class', 'legend-item');

      enterLegendItem
        .append('span')
        .attr('class', 'legend-key')
        .style('background-color', d => this.colorRange(d.value));

      enterLegendItem
        .append('span')
        .attr('class', 'legend-label')
        .html(d => {
          // return this.legendLabelFormat === null ? d.label : this.legendLabelFormat(d.label);
          switch (this.legendLabelFormatType) {
            case 'number':
              return this.legendLabelFormat(d.label);

            case 'time':
              const parsedTime = d3_isoParse(d.label);
              return this.legendLabelFormat(parsedTime);

            default:
              return d.label;
          }
        });

      enterLegendItem
        .on('mouseover', (data, index, nodes) => this.legendMouseOver(d3_event, data, index, nodes))
        .on('mouseout', () => this.legendMouseOut())
        .on('click', (data, index, nodes) => this.legendMouseClick(d3_event, data, index, nodes));
    }

    if (this.threshold) {
      this.yThreshold
        .raise()
        .transition()
        .duration(1000)
        .attr('transform', `translate(0,  ${this.yAxisScale(+this.threshold)})`);
    }

    if (this.average) {
      this.yAverage
        .raise()
        .transition()
        .duration(1000)
        .attr('transform', `translate(0,  ${this.yAxisScale(+this.average)})`);
    }
  };

  barMouseOverFocus = (event, data, index, nodes) => {
    this.chart
      .selectAll('.bar-group')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    const bar = this.chart
      .selectAll('.bar-group')
      .filter((d, i) => i === index)
      .select('.bar');

    const barColor = bar.attr('data-color');

    bar.style('fill', () => barColor);

    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    this.tooltipShow(data, nodes.filter((d, i) => i === index));

    this.hovered.emit({ event, data });
  };

  barMouseOutBlur = () => {
    this.chart
      .selectAll('.bar-group')
      .classed('inactive', false)
      .select('.bar')
      .style('fill', null);

    this.chart.selectAll('.legend-item').classed('inactive', false);

    this.tooltipHide();
  };

  barMouseClick = (event, data, index, nodes) => {
    this.clicked.emit({ event, data });
  };

  legendMouseOver = (event, data, index, nodes) => {
    this.chart
      .selectAll('.legend-item')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    this.chart
      .selectAll('.bar-group')
      .filter((d, i) => i !== index)
      .classed('inactive', true);

    const bar = this.chart
      .selectAll('.bar-group')
      .filter((d, i) => i === index)
      .select('.bar');

    const barColor = bar.attr('data-color');

    bar.style('fill', () => barColor);

    this.tooltipShow(data, this.chart.selectAll('.bar').filter((d, i) => i === index)._groups[0]); // TODO: find better way than using _groups

    this.hovered.emit({ event, data });
  };

  legendMouseOut = () => {
    this.chart.selectAll('.legend-item').classed('inactive', false);

    this.chart
      .selectAll('.bar-group')
      .classed('inactive', false)
      .select('.bar')
      .style('fill', null);

    this.tooltipHide();
  };

  legendMouseClick = (event, data, index, nodes) => {
    this.clicked.emit({ event, data });
  };

  private tooltipShow = (data, node) => {
    let dimensions = node[0].getBoundingClientRect();
    let scroll = this._scroll.getScrollPosition();
    let label;

    switch (this.tooltipLabelFormatType) {
      case 'number':
        label = this.legendLabelFormat(data.label);
        break;

      case 'time':
        const parsedTime = d3_isoParse(data.label);
        label = this.legendLabelFormat(parsedTime);
        break;

      default:
        label = data.label;
    }
    let value =
      this.tooltipValueFormat === null
        ? `<div class="tooltip-value">${data.value}</div>`
        : `<div class="tooltip-value">${this.tooltipValueFormat(data.value)}</div>`;

    this.tooltip.html(
      `
        ${this.hideTooltipLabel ? '' : `${label}`}
        ${value}
      `
    );

    let tooltipOffsetWidth = +this.tooltip.node().offsetWidth / 2;
    let tooltipOffsetHeight = +this.tooltip.node().offsetHeight + 8;

    this.tooltip.style('top', `${+scroll[1] + +dimensions.top - tooltipOffsetHeight}px`); //
    this.tooltip.style('left', `${+scroll[0] + +dimensions.left - tooltipOffsetWidth + +dimensions.width / 2}px`);
    this.tooltip.style('opacity', 1);
  };

  private tooltipHide = () => {
    this.tooltip.style('opacity', 0);
  };

  private xAxisFormatter = item => {
    switch (this.xAxisFormatType) {
      case 'number':
        return this.xAxisFormat(item);

      case 'time':
        const parseDate = d3_isoParse(item);
        return this.xAxisFormat(parseDate);

      default:
        return item;
    }
  };

  private yAxisFormatter = item => {
    switch (this.yAxisFormatType) {
      case 'number':
        return this.yAxisFormat(item);

      case 'time':
        const parseDate = d3_isoParse(item);
        return this.yAxisFormat(parseDate);

      default:
        return item;
    }
  };
}
